import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Revisao_programa_qualidade } from '../../../modelo/Revisao_programa_qualidade'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'revisao_programa_qualidade-dados-edit',   templateUrl: 'revisao_programa_qualidade_dadosEdit.component.html' }) export class Revisao_programa_qualidadeDadosEditComponent implements OnInit {    revisao_programa_qualidade: Revisao_programa_qualidade;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.revisao_programa_qualidade.Revisao_programa_qualidade";                 let id = +params['id'];         this.revisao_programa_qualidade = new Revisao_programa_qualidade();         this.revisao_programa_qualidade.cd_revisao_programa_qualidade = id;         o.objetoJson = this.revisao_programa_qualidade;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a revisao_programa_qualidade. ';       }      this.revisao_programa_qualidade = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


